@import "variables";
@import "neo";

// Future on Framework --------
body {
  word-break: break-all;
}
.btn {
  // http://okakacacao.wpblog.jp/technology/safari-button-display-flex
  display: inline-flex; // NG on Safari
  align-items: center;
  justify-content: center;
  max-width: 200px;
  > span {
    overflow: hidden;
    text-overflow: ellipsis;
  }
  > .fa-caret-down {
    margin-left: 4px;
  }
  &.btn-center {
    // Please remove further future.
    display: inline-block;
  }
}
#navbar, .navibar {
  .navbar-body {
    .centerArea {
      padding: 0 15px;
    }
    .rightArea {
      flex: 1;
      display: flex;
      justify-content: flex-end;
      .btn {
        // Please remove further future.
        display: inline-block;
      }
    }
  }
}

[v-cloak] {
  display: none !important;
}


//----------------------------------------
// Swift
//
.ly-swift {
  .mgt {
    margin-top: 15px;
  }
  .mgb {
    margin-bottom: 15px;
  }
}


// ----------------------------
.mk-notification-digest {
  margin-bottom: 8px;

  .n10n__list {
    max-height: 174px;
    overflow-y: auto;
  }
  .n10n__item {
    display: flex;
    align-items: center;
    padding-left: 15px;
    border-bottom: 1px solid $gBorderColor;

    &:hover {
      .n10n__dismiss .fa-times-circle {
        opacity: 1;
      }
    }
  }
  .n10n__text {
    flex: 1;
    padding: 8px 15px 8px 0;
  }
  .n10n__sentat {
    font-size: 12px;
  }
  .n10n__dismiss {
    width: 45px;

    .fa-times-circle {
      opacity: 0;
      transition: all 0.3s ease;
    }
  }
}

.sh-status-chip {
  display: block;
  padding: 4px 6px;
  border-radius: 2px;
  color: $gColorBlack;
  background-color: $gColorPaleGray;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s ease;

  &:hover {
    box-shadow: 0 3px 6px 0 rgba(0,0,0,.3);
    transition: all 0.3s ease;
    color: $gColorBlack;
    text-decoration: none;
  }

  &.isHold {
    color: #fff;
    font-weight: bold;
    background-color: $gAccentColorLighter;
    
    &:hover {
      color: #fff;
    }
  }
}

