/* Modal
===================================================== */
#modal-overlay {
	display: none;
	position: fixed;
	z-index: 99998;
	top: 0;
	left: 0;
	width: 100%;
	background-position: center center;
	background-repeat: no-repeat;
	background: -webkit-radial-gradient(circle, #aaa, #0c2448);
	filter: alpha(opacity=30);
	background-color: #000;
	height: 100%;
	visibility: visible;
	opacity: 0.3;
}
#disabledWrap.clickToReload {
	cursor: pointer;
}
.modal-body-src {
	display: none;
}
#modal-window {
	display: none;
	width: 500px;
	left: 20px;
	top: 20px;
	position: absolute;
	position: fixed;
	margin: 0;
	color: #808080;
	font-size: 13px;
	font-weight: normal;
	line-height: 18px;
	background-color: #FFFFFF;
	border: 1px solid #EAEEFA;
	border: 1px solid rgba(234, 238, 250, 0.6);
	-webkit-border-radius: 6px;
	border-radius: 6px;
	-webkit-box-shadow: 0 0 3px rgba(0, 0, 0, 0.6);
	box-shadow: 0 0 3px rgba(0, 0, 0, 0.6);
	z-index: 99999;
	
	&.modal-body {
		padding: 10px 20px 0;
	}
	.closearea {
		text-align: right;
		font-size: 18px;
		
		.fa-close:hover {
			color: #4facbd;
		}
	}
	.modal-header {
		border: 0;
		
		p {
			margin: 5px 0;
			font-size: 12px;
		}
	}
	.modal-footer {
		padding: 20px 0;
		text-align: center;
		background: inherit;
		border: 0;
	}
}

