/* Tab
===================================================== */
.tab {
	margin: 0 0 10px;
}
.tab ul {
	margin: 0;
	list-style-type: none;
}
.tab ul li:first-child {
	margin-left: 20px;
}
.tab ul li {
	display: block;
	background-color: #e7edef;
	margin: 0 0 0 15px;
	float: left;
}
.tab ul li a {
	display: inline-block;
	width: inherit;
	height: inherit;
	padding: 8px 15px;
}
.tab ul li a:hover {
	background-color: #d3efff;
}
.tab ul li.active {
	background-color: #fff;
	border-left: 1px solid #ccc;
	border-right: 1px solid #ccc;
	border-bottom: 1px solid #fff;
	margin-bottom: -2px;
}
.tab ul li.active a {
	color: #666;
	font-weight: bold;
	text-decoration: none;
	border-top: 3px solid #4facbd;
	padding-top: 6px;
}
.tab .separater {
	clear: both;
	margin-top: -21px;
	border-top: 1px solid #ccc;
}

/* hideWhenBlank
===================================================== */
.hideWhenBlank {
	display: none;
}

/* Pop Help
===================================================== */
.popInfoWrap {
	position:relative;
	z-index: 500;
}
.popInfo {
	position: absolute;
	top:0; left:0;
	padding: 5px;
	width: 300px;
	border: 1px solid #404040;
	font-size:12px;
	font-weight: normal;
	text-align:left;
	text-decoration: none;
	color: #000000;
	background-color: #FFFFCC;
	// background-image: url(../img/popHelpX.gif);
	background-repeat: no-repeat;
	background-position: top right;
	cursor: pointer;
}
.popSwitch {
	color: #006091;
	text-decoration: underline;
	font-weight: normal !important;
	cursor: pointer;
}

/* HyokaSheet Header
===================================================== */
.hyokasheet-header {
	margin: 20px;
	color: #666;
	font-family: sans-serif;
}

/* -- titleArea ------------------------------------- */
.hyokasheet-header .titleArea {
	width: 100%;
}
.hyokasheet-header .titleArea .cellLeft {
	width: 55%;
	padding: 10px 0;
}
.hyokasheet-header .titleArea .cellLeft ul {
	background-image: url(../img/paper.png);
	background-repeat: no-repeat;
	padding: 5px 0 5px 75px;
	list-style-type: none;
	line-height: 1.4;
	font-weight: bold;
}
.hyokasheet-header .titleArea .cellLeft ul li.sheet-title-operation {
	font-size: 18px;
}
.hyokasheet-header .titleArea .cellLeft ul li.sheet-title-form {
	font-size: 24px;
}
.hyokasheet-header .titleArea .cellRight {
	width: 32%;
	background-color: #f2f2f2;
	padding: 15px 25px;
}
.hyokasheet-header .sheet-header-info th,
.hyokasheet-header .sheet-header-info td {
	text-align: left;
	font-weight: normal;
	padding: 3px;
}
.hyokasheet-header .sheet-header-info td {
	padding-left: 20px;
}
.hyokasheet-header .refSheetLinks {
	padding: 5px 0 0 75px;
}
.hyokasheet-header .refSheetLinks a {
	margin: 0 10px 0 0;
}
div.hyokasheetInner {
	margin: 20px;
}

/* HyokaSheet Status
===================================================== */
.sheet-status {
	margin: 20px 0;
	padding: 10px 0 20px;
	overflow: auto;
	overflow-y: hidden;
	
	th, td {
		text-align: center;
		white-space: nowrap;
	}
	.sheet-status-layout {
		margin: 0 auto;
		border-collapse: separate;
		
		td {
			vertical-align: top;
		}
		.sheet-status-col {
			margin: 0 10px;
			text-align: center;
			border-spacing: 1px;
			border-collapse: separate;
			width: 120px;
			box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.1);
			
			td {
				padding: 0 5px;
				background-color: #fff;
				vertical-align: middle;
			}
			.sheet-status-active {
				background-color: #36b1d2;
				color: #fff;
				text-shadow: 0 1px 1px #444;
				padding: 10px;
				border-bottom: 1px solid #2787a0;
			}
			.sheet-status-inactive {
				background-color: #758d93;
				color: #fff;
				text-shadow: 0 1px 1px #444;
				padding: 10px;
				border-bottom: 1px solid #5a6a6e;
			}
			.sheet-status-person {
				color: #444;
				font-size: 12px;
				padding: 0 10px;
				line-height: 2.5;
			}
			&.legend th,
			&.legend td {
				text-align: right;
				background: none;
				color: #333;
				text-shadow: none;
				font-weight: normal;
				border: 0;
				border-right: 1px solid #ccc;
			}
		}
	}
}
span.timestamp {
	font-size: 10px;
	line-height: 1;
	margin: 0 0 10px;
	display: block;
}
